:root{
  /* ### Primary  */
  --Bright-Red: hsl(12, 88%, 59%) ;
  --Dark-Blue: hsl(228, 39%, 23%);
  /* ### Neutral  */
  --Dark-Grayish-Blue: hsl(227, 12%, 61%);
  --Very-Dark-Blue: hsl(233, 12%, 13%);
  --Very-Pale-Red: hsl(13, 100%, 96%);
  --Very-Light-Gray: hsl(0, 0%, 98%);
  /* ## Typography */
}

@font-face {
  font-family: light;
  src: url(fonts/Poppins-Light.ttf);
}

@font-face {
  font-family: normal;
  src: url(fonts/Poppins-Medium.ttf);
}

@font-face {
  font-family: bold;
  src: url(fonts/Poppins-Bold.ttf);
}

body{
  margin: 0;
  padding: 0;
}

nav{
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 6rem;
  justify-content: space-between;
  padding-left: 70px;
    padding-right: 70px;
    box-sizing: border-box;
}

.logo{
  display: flex;
  align-items: center;
}

.ham{
  display: none;
  background: none;
  border: none;
  background-size: 90px 90px;
}


ul{
  list-style: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 3rem;
}

li{
  font-family: light;
}

.navLinks{
  color: var(--Dark-Blue);
  border-bottom-style: solid;
  border-bottom-color: white;
  border-width: medium;
}

.navLinks:hover{
  transition: .2s;
  color: var(--Dark-Grayish-Blue);
  cursor: pointer;
  border-bottom-style: solid;
  border-bottom-color: var(--Bright-Red);
  border-width: 1.5px;
}

.orange{
  color: white;
  background-color: var(--Bright-Red);
  border: none;
  height: 2.5rem;
  width: 7.5rem;
  border-radius: 1.5rem;
  font-family: normal;
}

.orange:hover{
  transition: .5s;
  background-color: white;
  border-style: solid;
  border-width: thin;
  border-color: var(--Bright-Red);
  color: var(--Bright-Red);
  cursor: pointer;
}

.oranged{
  background-color: white;
    color: var(--Bright-Red);
    border: none;
    height: 2.5rem;
    width: 7.5rem;
    border-radius: 1.5rem;
    font-family: normal;
}

.oranged:hover{
  cursor: pointer;
}

.orangeb:hover{
  cursor: pointer;

}

section{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 70px;
    padding-right: 70px;
    box-sizing: border-box;
}

.everyone{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: left;
  gap: 1rem;
}

h3{
  font-family: bold;
  color: var(--Dark-Blue);
  font-size: xx-large;
  margin: 0;
}

p{
  color: var(--Dark-Grayish-Blue);
  font-family: normal;
  margin: 0;
}

ul{
  margin: 0;
  padding: 0;
}

.questions{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  height: auto;
}

.question{
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 40%;
}

.answer{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 55%;
}

.num{
  width: 60px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--Bright-Red);
  color: white;
  border-radius: .9rem;
}
.flex{
  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: space-between;
}
.text{
  width: 90%;
}

.blue{
  color: var(--Dark-Blue);
  font-weight: 600;
}
.people{
  width: 60px;

  height: 60px;
  border-radius: 50%;
  background-size: 100% 100%;
}
.testimonial{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: auto;
  gap: 2rem;
  margin-top: 6rem;
  margin-bottom: 6rem;
  box-sizing: border-box;
  padding-left: 70px;
    padding-right: 70px;
    box-sizing: border-box;
}

.reviews{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.review{
  background-color: var(--Very-Light-Gray);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 32%;
  padding: 1rem;
  box-sizing: border-box;
}

.hover{
  color: white;
}

.hover:hover{
  color: var(--Bright-Red);
  cursor: pointer;
}

.linkb:hover{
  cursor: pointer;
  color: var(--Bright-Red);
}

footer{
  display: flex;
  flex-direction: column;
}

.red{
  width: 100%;
  background-color: var(--Bright-Red);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 9rem;
  align-items: center;
  padding-left: 70px;
    padding-right: 70px;
    box-sizing: border-box;
}
.white{
  color: white;
  font-size: x-large;
}

.black{
  background-color: var(--Very-Dark-Blue);
  color: white;
  padding-left: 70px;
    padding-right: 70px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    height: 12rem;
    align-items: center;
    justify-content: space-between;
}

.foot-logo{
  display: flex;
  flex-direction: column;
  height: 50%;
  justify-content: space-between;
}

.links{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.link{
  display: flex;
  flex-direction: column;
  gap: 0;
  height: 100%;
  justify-content: space-between;
}

.form{
  display: flex;
  flex-direction: column;
  height: 60%;
  justify-content: space-between;
}
form{
  display: flex;
  /* justify-content: space-between; */
  gap: 1rem;
  align-items: center;
}
input{
background-color: white;
border: none;
height: 3rem;
width: 12rem;
border-radius: 2rem;
display: flex;
align-items: center;
justify-content: center;
margin: 0;
padding: 0;
}

.orangec {
  color: white;
  background-color: var(--Bright-Red);
  border: none;
  height: 2.5rem;
  width: 3.5rem;
  border-radius: 1.5rem;
  font-family: normal;
}

.orangec:hover {
  transition: .5s;
  background-color: white;
  border-style: solid;
  border-width: thin;
  border-color: var(--Bright-Red);
  color: var(--Bright-Red);
  cursor: pointer;
}
.b{
  height: 60%;
  width: 25rem;
  text-align: left;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media screen and (max-width: 600px) {
  body{
    padding: 0;
  }
  .section{
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
      align-items: center;
  }

  .orangeb{
    display: none;
  }

  .section{
    margin: 0;
    padding: 1rem;
    padding-bottom: 10rem;
    height: auto;
  }
  h3{
    font-size: xx-large;
    font-weight: 700;
  }

  nav{
    justify-content: center;
    box-sizing: border-box;
    padding-left: 1rem;
    padding-right: 1rem;
  }
  .ul-hide{
    margin-top: -100vh;
    width: 90%;
    display: flex;
    flex-direction: column;
    position: absolute;
    background: white;
    align-self: center;
    justify-content: center;
    transition: .3s;
    width: 85%;
    height: 20rem;
  /* box-shadow: 7px 7px 12px; */
    border-style: solid;
    border-color: var(--Dark-Grayish-Blue);
    border-width: thin;
      border-radius: 1rem;
  }

  .ul-show{
    display: flex;
    flex-direction: column;
    position: absolute;
    background: white;
    align-self: center;
    justify-content: center;
    transition: .3s;
    margin-top: 30rem;
    width: 85%;
    height: 20rem;
    /* box-shadow: 7px 7px 12px; */
    border-style: solid;
    border-color: var(--Dark-Grayish-Blue);
    border-width: thin;
    border-radius: 1rem;
  }

  .ham{
    display: block;
  }

  .logo{
    justify-content: space-between;
    width: 100%;
  }

  .navLinks{
    font-size: larger;
    margin: 0;
  }
  .svgs{
    width: 90%;
    background-size: contain;
  }

  .intro{
    width: 100%;
    background-size: contain;
  }

  .black{
    flex-direction: column;
  }

  .questions{
    padding-left: 2rem;
    padding-right: 2rem;
  }

  .testimonial{
    padding-left: 2rem;
    padding-right: 2rem;
  }

  .everyone{
    text-align: center;
    gap: 2rem;
    align-items: center;
  }
  p{
    font-size: larger;
    margin: 0;
  }

  .orange{
    height: 3.5rem;
    width: 14rem;
  }

  .questions{
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  }
  .question{
    width: 100%;
    align-items: center;
    text-align: center;
    gap: 1rem;
  }
  .flex{
    width: 100%;
  }
  .answer{
    width: 100%;
  }
  .reviews{
    flex-direction: column;
    width: 100%;
  }
  .review{
    width: 100%;
  }

  .red{
    display: flex;
    flex-direction: column;
    height: 12rem;
    padding: 2rem;
  }

  .black{
    height: auto;
    padding: 1rem;
    flex-direction: column-reverse;
    gap: 2rem;
    box-sizing: border-box;
  }
  .b{
    width: 90%;
  }
  input{
    width: 17rem;
  }

  .foot-logo{
    flex-direction: column-reverse;
    gap: 2rem;
    width: 90%;
  }
  .form{
    gap: 2rem;
  }
  form{
    flex-direction: column;
  }
  .orangec{
    width: 40%;
  }
}
